
import back from '@/components/back'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
data() {
return {
tabItem: [],
active: null,
selectPatient: this.$store.state.patient,
info:null,
drInfos:null,
temp:false,
temp1:false,
flag:0,
showConfirm: false, //是否显示弹窗
showPopup:false,//是否显示时间选择器
dateVal:"",
loginDrMsg:this.$store.state.userInfo.Data.User,
selectDr:null,
referralRemark:"",
primaryDr: null, //基层医生
haveLevel:false,
isShowReferral:false
};
},
directives: {
TransferDom
},
components: {
back
},
created() {
let drInfoList = this.$store.state.ProDrInfos
let disGroupMsg = this.$store.state.DisGroupMsg
if(this.$store.state.proLevel != null){
this.loginDrMsg.ProfessionalLevel = this.$store.state.proLevel
}else{
this.$toast("缺失医生级别")
return
}
if(drInfoList && disGroupMsg && (Number(disGroupMsg.DisGroupId) == Number(this.$route.query.DisGroupId))) {
this.drInfos = drInfoList
this.flag = 6
this.primaryDr = disGroupMsg.primaryDr
} else {
this.pullDiseGroup(this.selectPatient.PatientId);
}


},
mounted() {

},
methods: {
tabOn(idx) {
this.active = idx
this.temp = false
this.flag = 0
this.pullDr(this.selectPatient.PatientId);
},
pullDiseGroup(patientId) {

this.$get('/PlatFormAPI/DiseaseGroupAndRule/QueryPatientDisGroup', {

PatientId: patientId,
}).then(o => {
if(o && o.length){
this.active = o[0]
this.pullDr(patientId)
}
this.tabItem = o || []
if(o && o.length > 4){
console.log(this.$refs.box)
this.$refs.box.style.width = 25 * o.length + "%"
}
this.temp1 = true
})
},
pullDr(patientId){

this.isShowReferral = false
let primaryDr = null
this.$get('/PlatFormAPI/DoctorReferral/QueryPatientDocReferralSet', {
DisGroupId:this.active.DisGroupId,
PatientId: patientId,
DrId:this.loginDrMsg.DoctorId
}).then(o => {
this.info = o
this.temp = true
if(this.info && this.info.DrInfos){
this.drInfos = []
this.drInfos.length = 5
this.info.DrInfos.forEach((v,idx) => {
let n = ""
this.drInfos[v.ProfessionalLevel - 1] = v
switch(v.ProfessionalLevel){
case 1:
n = '基层医生'
break;
case 2:
n = '乡级医生'
break;
case 3:
n = '区级医生'
break;
case 4:
n = '市级医生'
break;
case 5:
n = '省级医生'
break;
default:
break;
}
this.drInfos[v.ProfessionalLevel - 1].ProfessionalName = n
if(v.ProfessionalLevel == 1){
primaryDr = v.DrId
this.primaryDr = v.DrId
}
if(v.DrId == this.loginDrMsg.DoctorId && !this.isShowReferral){
this.isShowReferral = true
}
this.flag ++
/*if(!v.DrId && primaryDr){
let val = v
this.$post('/PlatFormAPI/DoctorReferral/QueryCountDoctorReferral', {
DrId: primaryDr,
ProfessionalLevels:val.ProfessionalLevel,
DisGroupId:this.active.DisGroupId,
}).then(o => {
this.drInfos[val.ProfessionalLevel - 1].drNum = o[0].Num.toString()
this.flag ++

})
}else{
this.flag ++
}*/
})
}
this.temp = true
})
},
deterDate(){
this.showPopup=false

let date = new Date();
let seperator1 = "-";
let year = date.getFullYear();
let month = date.getMonth() + 1;
let strDate = date.getDate();
if(month >= 1 && month <= 9) {
month = "0" + month;
}
if(strDate >= 0 && strDate <= 9) {
strDate = "0" + strDate;
};

if(!this.dateVal){
this.dateVal = year + seperator1 + month + seperator1 + strDate;
}
},
Referral(i){

this.showConfirm = true
this.referralRemark = ""
this.selectDr = i
},
onConfirm(){

if(!this.referralRemark || this.referralRemark == ""){
return this.$toast("请填写转诊说明")
}
this.showConfirm = false
let param = this.active
this.drInfos.forEach((n, idx) => {
if(n.ProfessionalLevel) {
switch(n.ProfessionalLevel) {
case 1:

param.BasicDrId = n.DrId || null
param.BasicDrName = n.DrName || null
break;
case 2:

param.CountryDrId = n.DrId || null
param.CountryDrName = n.DrName || null
break;
case 3:

param.AreaDrId = n.DrId || null
param.AreaDrName = n.DrName || null
break;
case 4:

param.CityDrId = n.DrId || null
param.CityDrName = n.DrName || null
break;
case 5:

param.ProvinceDrId = n.DrId || null
param.ProvinceDrName = n.DrName || null
break;
default:
break;
}
}
})
param.FromDrId = this.loginDrMsg.DoctorId
param.FromDrName = this.loginDrMsg.DoctorName
param.ToDrId = this.selectDr.DrId
param.ToDrName = this.selectDr.DrName
param.ReferralRemark = this.referralRemark
param.PatientId = this.info.PatientId
param.PatientName = this.info.PatientName


this.$post('/PlatFormAPI/DoctorReferral/SavePatientReferralSet',param).then(o => {

this.$toast("转诊成功")
this.$bridge.callhandler('GradedReferralsRollout', "确认转诊")
})
},
goDrList(i) {

if(this.loginDrMsg.ProfessionalLevel != 1 || this.primaryDr != this.loginDrMsg.DoctorId){

return false;
}
if(i.ProfessionalLevel == 1){

return false;
}
this.$store.dispatch('pushProDrInfos', this.drInfos)
this.$router.push({
path: "/doctor-list",
query: {
id: this.primaryDr,
level: i.ProfessionalLevel,
DisGroupId: this.active.DisGroupId,
DisGroupName: this.active.DisGroupName,
PatientId: this.info.PatientId,
PatientName: this.info.PatientName,
fromWay:"setPat"
}
})
}
}
};



